<template>
  <div class="hello">
    <div class="emoji">
      <h1 v-show="smile" @click="change">
        <i class="fa fa-smile-o fa-5x fa-rotate-45 fa-fw"></i>
      </h1>
    </div>
    <div class="emoji">
      <h1 v-show="!smile" @click="change">
        <i class="fa fa-smile-o fa-spin fa-5x fa-fw"></i>
      </h1>
    </div>
  </div>
</template>

<script>
  require('../../node_modules/font-awesome/css/font-awesome.min.css')
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      smile: true
    }
  },
  methods: {
    change() {
      this.smile = !this.smile;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
html, body {
  /* background-color: #000; */
}

h1, h2 {
  font-weight: normal;
  /* color: #F8F9F9; */
  font-size: 100px;
}

.emoji {
  text-align: center;
  top: 1px;
}

ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



